.card {
  padding: 32px 16px 17px ;
  width: 200px;
  border: 1px solid #f3f3f3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #fff;
  border-radius: 5px;
}

.card__image {
  margin: 0 3px 40px;
  width: 160px;
  height: 134px;
}

.card__title {
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  height: 36px;
  color: #060b35;
}

.card__code {
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
  color: #616070;
  margin: 4px 0 16px;
}

.card__evaluation {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.card__price {
  display: flex;
  justify-content: space-between;
  margin: 24px 0 0;
}

.card__price-text {
  height: 18px;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #616070;
}

.card__price-sum {
  height: 18px;
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: #060b35;
}

.card__reviews {
  font-weight: 400;
  font-size: 10px;
  line-height: 18px;
  color: #060b35;
  height: 16px;
}

.card__button-buy {
  margin: 16px 0 0;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  background-color: #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
}

.card__button-buy:hover {
  width: 165px;
  height: 39px;
  background-color: #fff;
  color: #00acdc;
  border: 1px solid #00acdc;
}
